import React from "react"

import { rhythm } from "../../utils/typography"
import Header from './header'

class Layout extends React.Component {
  render() {
    return (
      <div
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(24),
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
        }}
      >
        <Header {...this.props} />
        <main>{this.props.children}</main>
      </div>
    )
  }
}

export default Layout
